@font-face {
  font-family: TTLakes;
  src: url(./fonts/TTLakes-Regular.woff);
  font-weight: 400;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: TTLakes bold;
  src: url(./fonts/TTLakes-ExtraBold.woff);
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: TTLakes medium;
  src: url(./fonts/TTLakes-Medium.woff);
  font-weight: 400;
  font-style: normal;
  font-display: swap; }

body,
* {
  font-family: TTLakes;
  color: #1b1c21;
  margin: 0 auto;
  background-color: #f8f8f8;
  font-size: 16px; }

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0; }

[class$='--item--tablet'],
[class$='--item--PC'] {
  display: none; }

.link {
  text-decoration: none; }

[class$='--flex-center'] {
  display: flex;
  align-items: center; }

button {
  cursor: pointer;
  border: 0 !important;
  outline: 0 !important; }

.icon {
  background-color: #ff3e79;
  background-repeat: no-repeat;
  background-position: center;
  border: none;
  border-radius: 50%;
  width: 40px;
  height: 40px; }

.button_burger {
  background-image: url(./static/burger.svg); }

.button_contacts {
  background-image: url(./static/contacts.svg); }

.button_chat {
  background-image: url(./static/chat.svg); }

.button_profile {
  background-image: url(./static/profile.svg); }

.button_repair {
  background-image: url(./static/repair.svg); }

.button_checkstatus {
  background-image: url(./static/checkstatus.svg); }

.header {
  background-color: #fff;
  border-bottom: 1px solid #d9fff5; }

.header__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 16px; }

.header__item {
  margin-left: 16px; }

.header__item:first-child {
  margin-left: 0; }

.header__logo {
  height: 51px;
  width: 96px; }

.header__right .header__item--vertica-line--left::before {
  content: '';
  display: inline-block;
  height: 32px;
  width: 2px;
  background-color: #eaeaea;
  border-radius: 1px;
  margin: 0 16px; }

.main {
  padding-top: 23px; }

.main__title {
  font-family: TTLakes bold;
  font-weight: bold;
  font-size: 28px;
  display: flex;
  align-items: center; }

.main__title::before {
  content: '';
  display: inline-block;
  height: 40px;
  margin-right: 18px;
  margin-left: -2px;
  border-right: 4px solid #41f6d7;
  border-radius: 2px; }

.slide-menu {
  display: flex;
  overflow-x: auto;
  white-space: nowrap;
  padding-top: 24px; }

.slide-menu::-webkit-scrollbar {
  width: 0; }

.slide-menu__link {
  padding: 10px 16px;
  color: #7e7e82;
  font-weight: bold; }

.slide-menu__link:first-child {
  margin-left: 16px; }

.slide-menu__link:last-child {
  margin-right: 16px; }

.slide-menu__link--active {
  border: 2px solid #b8ffec;
  background-color: #fff;
  border-radius: 6px; }

.block p {
  font-size: 14px; }

.block__left-container {
  padding: 0 16px; }

.link__read-more {
  color: #000;
  font-weight: bold;
  display: inline-flex;
  margin: 20px 0; }

.link__read-more::before {
  content: url(./static/arrow-down.svg);
  margin: auto 15px;
  margin-left: 5px; }

.block__cps-img {
  width: 100%; }

.repair-brands__catalog {
  padding: 0 16px; }

.swiper-pagination {
  position: static; }

.swiper-pagination .swiper-pagination-bullet {
  background-color: #979797; }

.brand-card {
  border: 1px solid #eaeaea;
  border-radius: 6px;
  min-width: 224px;
  max-width: 240px;
  height: 72px;
  display: flex;
  justify-content: space-between;
  align-items: center; }

.brand-card:not(:last-child) {
  margin-right: 16px; }

.brand-card__button {
  margin-right: 16px;
  border: 2px solid #ff3e79;
  border-radius: 20px;
  width: 40px;
  height: 40px;
  background: url(./static/dropdown.svg) no-repeat center; }

.brand-card__image {
  display: block;
  margin-left: 16px; }

.show-more {
  color: #000;
  font-weight: bold;
  display: inline-flex;
  align-items: center; }

.repair-brands__show-more {
  display: none;
  margin-bottom: 34px; }

.repair-brands__show-more--show::before {
  content: url(./static/arrow-down.svg);
  margin-right: 9px; }

.repair-brands__show-more--close::before {
  content: url(./static/arrow-down.svg);
  margin-right: 9px;
  transform: rotate(180deg); }

.repair-brands::before {
  display: block;
  position: absolute;
  content: '';
  top: 0;
  width: 100%;
  height: 1px;
  background: #d9fff5; }

.repair-brands__header {
  font-weight: 400;
  text-transform: uppercase;
  color: #7e7e82;
  background: linear-gradient(270deg, #f8f8f8 0%, #fff 100%);
  padding: 16px;
  margin-bottom: 16px; }

@media screen and (min-width: 768px) {
  .header__container {
    padding: 25px; }
  [class$='--item--tablet'] {
    display: block; }
  .header__left .header__item--vertica-line--left::before {
    content: '';
    display: inline-block;
    height: 32px;
    width: 2px;
    background-color: #eaeaea;
    border-radius: 1px;
    margin-right: 16px; }
  .header__right .header__wrapper {
    display: flex; }
  .main__title::before {
    margin-right: 22px; }
  .slide-menu__link:first-child {
    margin-left: 24px; }
  .slide-menu__link:last-child {
    margin-right: 24px; }
  .block {
    display: flex;
    gap: 35px;
    padding: 0 24px; }
  .block__left-container {
    padding: 0; }
  .block__text--item--tablet {
    margin-top: 25px; }
  .block__cps-img {
    min-width: 360px;
    border-radius: 5px; }
  .repair-brands__show-more {
    display: flex; }
  .repair-brands__wrapper {
    margin-bottom: 24px; }
  .repair-brands__catalog {
    padding: 0 24px; }
  .brand-card:not(:last-child) {
    margin-right: 0; }
  .repair-brands__header {
    padding: 16px 24px;
    margin-bottom: 24px; }
  .repair-brands__catalog--grid {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    justify-items: center;
    gap: 16px;
    width: 100%; }
  .brand-card--table-hidden {
    display: none; }
  .brand-card--table-show {
    display: flex; } }

@media screen and (min-width: 1120px) {
  body {
    max-width: 1120px; }
  .item_PC {
    display: block; }
  .header {
    display: none; }
  .main {
    padding: 40px 30px;
    background-color: #f8f8f8; }
  .main__header {
    display: flex;
    justify-content: space-between;
    align-items: center; }
  .main__title::before {
    margin-left: -30px; }
  .main__header-buttons {
    display: flex; }
  .main__header-button {
    display: flex;
    align-items: center;
    color: #000;
    border: none;
    font-size: 16px;
    font-weight: bold;
    margin-left: 30px; }
  .main__header-button span {
    margin-right: 10px; }
  .slide-menu {
    flex-wrap: wrap;
    padding-bottom: 0; }
  .slide-menu__link,
  .slide-menu__link:last-child,
  .slide-menu__link:first-child {
    margin: 0;
    flex-grow: 1; }
  .block {
    padding: 0;
    padding-top: 24px; }
  .block__cps-img {
    min-width: 520px; }
  .block__text--item--PC {
    display: inline; }
  .repair-brands__catalog {
    padding: 0 32px; }
  .repair-brands__header {
    padding: 16px 32px; }
  .brand-card--table-hidden {
    display: flex; }
  .repair-brands__catalog--grid {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr; }
  .brand-card--pc-hidden {
    display: none; }
  .brand-card--pc-show {
    display: flex; } }
